@import 'variables.module.scss';

.wrapper {
  background: $gray-bg-xx-light;
  color: $gray-text;
  padding: 80px 0 100px;
  h1 {
    text-align: center;
    margin-bottom: 100px;
  }
  h2 {
    text-align: center;
  }
  p {
    text-align: center;
    line-height: 33px;
    margin-bottom: 130px;
  }
  form {
    // display: flex;
    // align-items: center;
    // flex-direction: column;
    max-width: 850px;
    max-height: 1340px;
    margin: 0 auto;
    margin-top: -40px;
    // overflow: hidden;
    transition: $ease-200;
    @media only screen and (max-width: $break-medium) {
      width: 100%;
      max-height: 1564px;
      margin-top: -64px;
    }
    &.collapsed {
      max-height: 0;
    }
    .row {
      // border: 1px solid red;
      width: 100%;
      display: flex;
      // margin-bottom: 60px;
      @media only screen and (max-width: $break-small) {
        flex-wrap: wrap;
        // margin-bottom: 0;
      }
      > div {
        position: relative;
        flex-grow: 1;
        flex-shrink: 0;
        margin-top: 32px;
        &:first-child {
          padding-right: 32px;
        }
        &:last-child {
          padding-right: 0px;
        }
        @media only screen and (max-width: $break-small) {
          padding-right: 0 !important;
        }
        // flex-basis: 100%;
        // border: 1px solid red;
      }
      .col {

        // flex-grow: 1;
        // position: relative;
        // padding-top: 24px;
        @media only screen and (max-width: $break-small) {
          // margin-bottom: 60px;
        }
        &:first-child {
          // margin-right: 32px;
          @media only screen and (max-width: $break-small) {
            // margin-right: 0;
          }
        }
        &:last-child {
          // margin-right: 0;
        }
      }
    }
    input,
    select,
    textarea {
      border: 1px solid $gray-bg-dark;
      display: block;
      width: 100%;
      box-sizing: border-box;
      background: transparent;
      font-size: 20px;
      padding: 14px 32px;
      // position: relative;
      // top: 40px;
      margin-top: 32px;
      @media only screen and (max-width: $break-medium) {
        // top: 64px;
      }
      &[required] {
        + label:after {
          content: ' *';
          color: $red;
        }
      }
      &.error {
        border: 1px solid red;
      }
    }
    select {
      position: relative;
      z-index: 20;
      appearance: none;
    }
    textarea {
      height: 300px;
      resize: none;
      @media only screen and (max-width: $break-medium) {
        height: 500px;
      }
    }
    label {
      position: absolute;
      top: 0;
      left: 0;
      font-size: 16px;
      // padding-top: 140px;
      @media only screen and (max-width: $break-medium) {
        // padding-top: 64px;
      }
      &[for="source"] {
        width: 100%;
        z-index: 10;
        &:after {
          content: '';
          display: block;
          background-image: url('~assets/images/arrow-down-small.svg');
          background-repeat: no-repeat;
          background-position: 0 0;
          background-size: 100% auto;
          position: absolute;
          top: 54px;
          right: 16px;
          width: 24px;
          height: 24px;
        }
      }
    }
  }
  a {
    margin-top: 140px;
    @media only screen and (max-width: $break-medium) {
      margin-top: 64px;
    }
  }
}
